.block-day {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  font-size: 16px;
  color: var(--font-color);
}

// !!!!! DAY  !!!!!!!!!
/* ** Стили для текущего дня */
.day-list {
  box-shadow: var(--box-shedow-1);
  padding: 10px;
  padding-left: 15px;
  border-radius: 10px;
  padding-top: 20px;
  font-size: 18px;
}

.location {
  display: flex;
  align-items: baseline;
}

.location-line {
  content: '';
  height: 2px;
  color: red;
  display: block;
  width: 100%;
  background-image: linear-gradient(
    90deg,
    rgba(255, 128, 82, 1) 0%,
    rgba(255, 128, 82, 0) 100%
  );
}

.button-location {
  background: transparent;
  border: none;
  cursor: pointer;
}

//Стили для input поиска города
.inputbox {
  position: relative;
  width: 300px;

  & #city {
    position: relative;
    width: 100%;
    padding: 15px 10px 15px;
    background: transparent;
    outline: none;
    box-shadow: none;
    border: none;
    color: var(--font-color);
    letter-spacing: 0.05em;
    transition: 0.5s;
    z-index: 10;
  }

  & span {
    position: absolute;
    left: 0;
    padding: 20px 10px 10px;
    letter-spacing: 00.05em;
    transition: 0.5s;
    pointer-events: none;
  }

  & #city:valid ~ span,
  & #city:focus ~ span {
    transform: translateX(-10px) translateY(-34px);
    font-size: 14px;
    font-weight: 400;
  }

  & i {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 4px;
    transition: 0.5s;
    pointer-events: none;
    z-index: 9;
  }

  & #city:valid ~ i,
  & #city:focus ~ i {
    height: 44px;
  }
}

.button-search {
  background: transparent;
  border: none;
  color: var(--font-color);
  cursor: pointer;
}

.condition-text {
  text-align: right;
  margin-bottom: 0;
}

.condition {
  display: flex;
  font-size: 84px;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 30px;
}

.condition div p {
  font-size: 18px;
}

.condition img {
  width: 160px;
  height: 160px;
}

.condition-block-left {
  width: 190px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.condition-block-left span {
  margin-bottom: 20px;
}

.condition-block-small {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.condition-block-small p:nth-child(2n + 1) {
  margin-right: 5px;
}

.condition-block-item {
  display: flex;
  align-items: center;
  margin-bottom: 0px;
  margin-top: 10px;
}

.condition-block {
  width: 220px;
  padding-left: 10px;
  display: flex;
  flex-direction: column-reverse;
  text-align: right;
}

.condition-block img {
  margin-left: auto;
  display: block;
}

.condition-block-bottom {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  padding-right: 20px;
  margin-bottom: 20px;
}

.update-time {
  font-size: 12px;
}

