.month-precipitation {
  height: 40px;
  text-align: center;
}

.block-weather-detail {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}

.tabs-weather {
  display: flex;
  position: relative;
  padding: 0px 10px 10px 10px;
  border-radius: 99px;
  width: 1000px;
}

.tabs-weather * {
  z-index: 2;
}

.container-radio-weather input[type='radio'] {
  display: none;
}

.tab-weather {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: calc(100% / 2);
  font-size: 20px;
  color: var(--font-color);
  font-weight: 500;
  border-radius: 99px;
  cursor: pointer;
  transition: color 0.15s ease-in;
  padding-bottom: 3px;
}

.container-radio-weather input[type='radio']:checked + label {
  color: var(--accent-color-2);
}

.container-radio-weather input[type='radio']:checked + label > .notification {
  background-color: #185ee0;
  color: var(--accent-color-2);
  margin: 0px;
}

.container-radio-weather input[id='radio-1-weather']:checked ~ .glider-weather {
  transform: translateX(0);
}

.container-radio-weather input[id='radio-2-weather']:checked ~ .glider-weather {
  transform: translateX(100%);
}

.glider-weather {
  position: absolute;
  display: flex;
  height: 30px;
  width: calc(100% / 2);
  z-index: 1;
  border-radius: 10px;
  transition: 0.25s ease-out;
  background: var(--background-color);
  box-shadow: var(--box-shedow-1);
}

