#weather-day {
  margin-top: 65px;
}

#weather-hour {
  display: flex;
  flex-wrap: wrap;
  max-width: 1090px;
  justify-content: space-between;
}

.ico-location {
  fill: var(--font-color);
  margin-right: 10px;
}
.ico-thermometer {
  margin: 0;
}
svg {
  fill: var(--font-color);
  margin-right: 7px;
}

.hour-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  border-radius: 10px;
  box-shadow: var(--box-shedow-1);
  width: calc((100% - 100px) / 8);
  margin-right: 0;
  margin: 0;
  margin-bottom: 21px;
  flex-direction: column;
  padding: 10px 10px;
}

.hour-item:nth-last-child(-n + 8) {
  margin-bottom: 0px;
}

.hour-item-temp {
  font-size: 16px;
  margin-top: 8px;
  margin-bottom: 15px;
}

.hour-item-temp {
  font-size: 24px;
}

.first-day {
  color: var(--accent-color-2);
  display: flex;
  align-items: center;
  margin: 14px;
}

.undefined {
  color: #eb65df;
  display: flex;
  align-items: center;
  margin: 14px;
}

.icon-time {
  fill: currentColor;
}

