#weather-week {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-between;
  color: var(--font-color);
  font-size: 10px;
  max-width: 1600px;
  margin-right: auto;
  margin-left: auto;
}

#weather-week li,
#weather-hour li {
  font-size: 14px;
  list-style: none;
}

.condition-week {
  display: flex;
  font-weight: 500;
  justify-content: space-around;
  margin-bottom: 0px;
}

.condition-week p {
  font-size: 32px;
}

.condition-week img {
  width: 96px;
  height: 96px;
}
.week-first-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.week-first-item p {
  font-size: 14px;
  display: flex;
  align-items: center;
}

.week-humidity {
  display: flex;
  align-items: center;
}
.week-humidity img {
  width: 32px;
  height: 32px;
}

.icon-pressure {
  margin-right: 0;
}
